    display: flex;
    width: 100%;
    margin-bottom: 20px;

    &__search {
      flex-grow: 1;
    }
    &__date-range {
      margin: 0 20px;
    }
  }
  &__pagination {
    margin-bottom: 20px;
    margin-top: 50px;
  }
}
